.conteiner_partners-items {
  padding-inline: 25px;
}

.partners_body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-left: solid 1px black;
  border-right: solid 1px black;
  background-color: #fbb251;
}

.partners_body-items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px;
  box-shadow: 5px 5px 0px 2px rgb(0, 0, 0);
  background-color: white;
  border: solid 1px black;
  border-radius: 10px;
}
@media (max-width: 900px) {
  .partners_body-items {
    padding: 25px;
    max-width: 620px;
  }
}

.partners_body-title {
  padding-block-start: 90px;
  padding-block-end: 40px;
  text-align: center;
}

.partners_body-title h1 {
  font-size: 65px;
}
@media (max-width: 900px) {
  .partners_body-title h1 {
    font-size: 32px;
    padding-inline: 25px;
  }
}

.partner_item p {
  font-size: 26px;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
}
@media (max-width: 900px) {
  .partner_item p {
    font-size: 16px;
  }
}

.partner_item:not(:last-child) {
  position: relative;
}

.partner_item:not(:last-child)::after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: black;
}

.partner_item {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-block: 20px;
}

.partner_item img {
  width: 36px;
}
@media (max-width: 900px) {
  .partner_item img {
    width: 16px;
  }
}

.footer_body {
  text-align: center;
  padding-block-start: 50px;
  padding-block-end: 90px;
  padding-inline: 25px;
  max-width: 550px;
}
@media (max-width: 900px) {
  .footer_body {
    padding-block-start: 30px;
    padding-block-end: 60px;
  }
}

.footer_body-title h4 {
  font-size: 24px;
}
@media (max-width: 900px) {
  .footer_body-title h4 {
    font-size: 18px;
  }
}

.footer_body-text p {
  padding-block-start: 10px;
  font-size: 22px;
  font-family: Nunito Sans, sans-serif;
}
@media (max-width: 900px) {
  .footer_body-text p {
    font-size: 16px;
  }
}